.search{
    margin-top: 1rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.input {
    border: none;
    outline: none;
    margin: 0 .5rem;
    border-radius: 15px;
    padding: 1em;
    background-color: #ccc;
    -webkit-box-shadow: inset 2px 5px 10px rgba(0,0,0,0.3);
            box-shadow: inset 2px 5px 10px rgba(0,0,0,0.3);
    -webkit-transition: 300ms ease-in-out;
    -o-transition: 300ms ease-in-out;
    transition: 300ms ease-in-out;
  }
  
  .input:focus {
    background-color: white;
    -webkit-transform: scale(1.05);
        -ms-transform: scale(1.05);
            transform: scale(1.05);
    -webkit-box-shadow: 13px 13px 100px #969696,
               -13px -13px 100px #ffffff;
            box-shadow: 13px 13px 100px #969696,
               -13px -13px 100px #ffffff;
  }
  /* button code */
  .Btn {
    width: 140px;
    height: 40px;
    border: none;
    border-radius: 10px;
    background: -webkit-gradient(linear,left top, right top,from(#77530a),color-stop(#ffd277),color-stop(#77530a),color-stop(#77530a),color-stop(#ffd277),to(#77530a));
    background: -webkit-linear-gradient(left,#77530a,#ffd277,#77530a,#77530a,#ffd277,#77530a);
    background: -o-linear-gradient(left,#77530a,#ffd277,#77530a,#77530a,#ffd277,#77530a);
    background: linear-gradient(to right,#77530a,#ffd277,#77530a,#77530a,#ffd277,#77530a);
    background-size: 250%;
    background-position: left;
    color: #ffd277;
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    cursor: pointer;
    -webkit-transition-duration: 1s;
         -o-transition-duration: 1s;
            transition-duration: 1s;
    overflow: hidden;
  }
  
  .Btn::before {
    position: absolute;
    content: "search";
    color: #ffd277;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    width: 97%;
    height: 90%;
    border-radius: 8px;
    -webkit-transition-duration: 1s;
         -o-transition-duration: 1s;
            transition-duration: 1s;
    background-color: rgba(0, 0, 0, 0.842);
    background-size: 200%;
  }
  
  .Btn:hover {
    background-position: right;
    -webkit-transition-duration: 1s;
         -o-transition-duration: 1s;
            transition-duration: 1s;
  }
  
  .Btn:hover::before {
    background-position: right;
    -webkit-transition-duration: 1s;
         -o-transition-duration: 1s;
            transition-duration: 1s;
  }
  
  .Btn:active {
    -webkit-transform: scale(0.95);
        -ms-transform: scale(0.95);
            transform: scale(0.95);
  }


